<template>
  <div>
    <b-table
      :fields="fields"
      :items="tableData"
      :borderless="false"
      :outlined="true"
      :striped="true"
      responsive="sm"
      class="import-table"
      head-variant="dark"
    >
      <template v-slot:empty>
        <div class="text-center errorColor">
          {{ $t("GENRALDATAERRORMESSAGE") }}
        </div>
      </template>
      <template #thead-top="">
        <b-tr>
          <b-th
            v-for="(field, index) in fields"
            :key="field.key"
            class="text-left"
          >
            <b-button @click="itemSelectionClick(index)" variant="primary">
              {{ $t("ITEMSELECTION") }}
            </b-button>
          </b-th>
        </b-tr>
      </template>
      <template v-slot:[tableCells(field.key)]="item" v-for="field in fields">
        {{ item.value }}
      </template>
    </b-table>
  </div>
</template>

<script>
export default {
  name: "ImportTable",
  props: {
    tableData: {
      type: [Array, Function],
      default: () => [],
    },
    fields: {
      type: [Array, Object],
      default: () => [],
    },
    perPage: {
      type: Number,
      default: 5,
    },
  },
  methods: {
    tableCells(key) {
      return `cell(${key})`;
    },
    itemSelectionClick(item) {
      this.$emit("itemSelectionClick", item);
    },
  },
};
</script>
<style lang="scss">
.import-table {
  overflow: auto;
  width: 100%;
  overflow-y: auto;
  height: 300px;
}
.import-table thead tr:first-child th {
  border: none;
}
.import-table thead tr:nth-child(2) th {
  border: none;
}

thead tr:nth-child(1) th {
  position: sticky;
  top: 0;
}
thead tr:nth-child(2) th {
  position: sticky;
  top: 60px;
}
.import-table thead tr th {
  white-space: nowrap;
}
</style>
